<template>
  <div class="footerNav">
    <!-- 底部固定栏start -->
    <div class="clearFixed">
      <div class="floor-fixed">
        <router-link tag="div" to="/" class="floor-item" :class=" (result == 'index' || result == '') ? 'fixed-active' : ''" >
          <i class="iconfont icon-news"></i>
          <p>新闻</p>     
        </router-link>          
        <router-link tag="div" to="/love" class="floor-item" :class=" result == 'love' ? 'fixed-active' : ''">
          <i class="iconfont icon-love"></i>
          <p>推荐</p>
        </router-link>        
        <router-link tag="div" to="/live" class="floor-item" :class=" result == 'live' ? 'fixed-active' : ''">
          <i class="iconfont icon-live"></i>
          <p>直播</p>
         </router-link>      
        <router-link tag="div" to="/my" class="floor-item" :class=" result == 'my' ? 'fixed-active' : ''">
          <i class="iconfont icon-my"></i>
          <p>我</p>
         </router-link> 

      </div>
    </div>

    <!-- 底部固定栏end -->

  </div>
</template>

<script>
export default {
  name: 'FooterNav',
  computed: {
    result: function(){
      var arr = this.$route.path.split('/');
      return arr[1];
    }
  }

}
</script>

<style scoped lang="scss">
  @import '../../assets/css/torem.scss';
     /*底部固定栏start*/
     .clearFixed{
        &::before{
            content: "";
            display: block;
            width:100%; 
            height: torem(148px);
          } 
        .floor-fixed{
        position: fixed;
        bottom: -1px;
        left: 0;
        width:100%; 
        height: torem(108px);
        padding: torem(20px) 0;
        background-color: #fff;
        border-top: 3px solid #ebebeb;
          .floor-item{
            float: left;
            width: 25%;
            text-align: center;
            height: 100%;
            color: #727272;
            .iconfont{
              font-size: torem(54px);
            }
            p{
              margin-top: torem(20px);
            }
          }
          .fixed-active{
            color: #0b80e5;
          }
          
       }
     }

     /*底部固定栏end*/
</style>


