<template>
  <div class="login">
    <!-- 登录头部栏start -->
    <header class="recommend">
      <h3 class="title">登录</h3>
      <router-link to="/my" tag="div" class="fixed-rg" ><i class="iconfont icon-close"></i></router-link>
    </header>
    <!-- 登录页头部栏end -->
    <!-- 中间图标start  -->
    <div class="middle-img">
      <div class="img-item">
        <img src="../../../static/images/login/11.png" alt="">
        <p>QQ登录</p>
      </div>      
      <div class="img-item">
        <img src="../../../static/images/login/10.png" alt="">
        <p>微信登录</p>
      </div>
    </div>
    <!-- 中间图标end  -->
    <!-- 底部登录栏start -->
    <div class="login-content">
      <div class="cont-line">
        <div>或</div>
      </div>
      <div class="sub">
        <div class="user">
          <i class="iconfont icon-my"></i>
          <input type="text" name="username" value="" placeholder="QQ/腾讯新闻/邮箱">
        </div>
        <div class="password">
          <i class="iconfont icon-lock"></i>
          <input type="password" name="password" value="" placeholder="请输入密码">
        </div>
        <div class="submit">登录</div>
      </div>
    </div>

    <!-- 底部登录栏end -->
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted: function(){
    $(function(){
      $('input').focus(function(){
        $('.submit').addClass('sub-active');
      }).blur(function(){
        $('.submit').removeClass('sub-active');
      })
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../../assets/css/torem.scss';
  .login{
    /*登录页头部栏start*/
    header.recommend{
      position: relative;
      width: 100%;
      height: torem(146px);
      background-color: #fff;
      border-bottom: 2px solid #cbcbcb;
      margin-bottom: torem(80px);
      .title{
        line-height: torem(146px);
        font-size: torem(50px);
        color: #333;
        font-weight: bold;
        text-align: center;
      }
      .fixed-rg{
        position: absolute;
        top: 0;
        right: 0;
        line-height: torem(146px);
        margin-right: torem(40px);
        .icon-close{
          font-size: torem(60px);
          color: #676767;
          font-weight: bold;
        }
      }
    }
    /*登录页头部栏end*/
    /*中间图标start*/
    .middle-img{
      display: flex;
      justify-content: space-around;
      width: 100%;
      padding-top: torem(30px);
      margin-bottom: torem(50px);
      .img-item{
        text-align: center;
        img{
          width: torem(206px);
          height: torem(206px);
          border-radius: 50%;
          margin-bottom: torem(40px);
        }
        p{
          color: #6e6e6e;
          font-size: torem(40px);
          font-weight: bold;
        }
      }
    }
    /*中间图标end*/
    /*底部登录栏start*/
    .login-content{
      padding: torem(70px) torem(120px);
      background-color: #fff;
      .cont-line{
        position: relative;
        margin: 0 auto;
        height: 1;
        width: torem(420px);
        border-bottom: 2px solid #f0f0f0;
        margin-bottom: torem(110px);
        div{
          position: absolute;
          left: 50%;
          top: torem(-24px);
          margin-left: torem(-50px);
          width: torem(100px);
          height: torem(48px);
          line-height: torem(48px);
          background-color: #fff;
          text-align: center;
          font-size: torem(40px);
          color: #c8c8c8;
        }
      }
      .sub{
        .user{
          display: flex;
          height: torem(102px);
          border: 2px solid #f0f0f0;
          border-radius: torem(5px);
          margin-bottom: torem(62px);
          .icon-my{
            width: torem(102px);
            height: torem(102px);
            text-align: center;
            line-height: torem(102px);
            font-size: torem(50px);
            color: #c7c7c7;
          }
          input{
            width: torem(440px);
            height: torem(102px);
            border: none;
            font-size: torem(50px);
            color: #999;
            &::placeholder{
              color: #c7c7c7;
            }
          }
        }
        .password{
          display: flex;
          height: torem(102px);
          border: 2px solid #f0f0f0;
          border-radius: torem(5px);
          margin-bottom: torem(120px);
          .icon-lock{
            width: torem(102px);
            height: torem(102px);
            text-align: center;
            line-height: torem(102px);
            font-size: torem(50px);
            color: #c7c7c7;
          }
          input{
            width: torem(440px);
            height: torem(102px);
            border: none;
            font-size: torem(50px);
            color: #999;
            &::placeholder{
              color: #c7c7c7;
            }
          }
        }
        .submit{
          height: torem(106px);
          border-radius: 5px;
          background-color: #e4e4e4;
          color: #fff;
          font-size: torem(40px);
          text-align: center;
          line-height: torem(106px);
        }
        .sub-active{
          background-color: #17bcfa;
        }
      }
    }
    /*底部登录栏end*/

  }

</style>



