<template>
  <div class="my">
    <!-- 登录头部栏start -->
    <div class="my-header">
      <div class="header-icon">
        <router-link to="/login" tag="i" class="iconfont icon-xinxo"></router-link>
        <router-link to="/set" tag="i" class="iconfont icon-config"></router-link>
      </div>
      <router-link to="/login" tag="div" class="header-cont">
        <div class="cont-icon">
          <i class="iconfont icon-head-1"></i>
        </div>
        <div class="btn-login">点击登录</div>
      </router-link>
    </div>
    <!-- 登录头部栏end -->
    <!-- 中间功能栏start -->
    <div class="middle">
      <ul class="middle-cont">
        <li class="mid-item">
          <i class="iconfont icon-night"></i>
          <p>夜间模式</p>
        </li>        
        <li class="mid-item">
          <i class="iconfont icon-text"></i>
          <p>文字模式</p>
        </li>        
        <li class="mid-item">
          <i class="iconfont icon-load"></i>
          <p>离线阅读</p>
        </li>
      </ul>
    </div>
    <!-- 中间功能栏end -->
    <!-- 底部功能栏start -->
    <div class="my-floor">
      <router-link to="/login" tag="div" class="fl-item">
        <span>我的关注</span>
        <i class="iconfont icon-right"></i>
      </router-link>      
      <router-link to="/login" tag="div" class="fl-item">
        <span>我的收藏</span>
        <i class="iconfont icon-right"></i>
      </router-link>      
      <router-link to="/login" tag="div" class="fl-item">
        <span>我的评论</span>
        <i class="iconfont icon-right"></i>
      </router-link>
    </div>
    <!-- 底部功能栏end -->


    <!-- 底部固定栏引入 -->
  	<footerNav></footerNav>
  </div>
</template>

<script>
import footerNav from '@/components/public/FooterNav';

export default {
  name: 'My',
  data () {
    return {
      msg: '你好',
      name: 'asdfadsfads'
    }
  },
  components:{
    footerNav
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../assets/css/torem.scss';
  .my{
    /*登录头部栏start*/
    .my-header{
      height: torem(448px);
      padding: torem(54px) torem(54px);
      background-color: #7a99b7;
      .header-icon{
        display: flex;
        justify-content:space-between;
        margin-bottom: torem(80px);
        .iconfont{
          font-size: torem(64px);
          color: #fff;
        }
      }
      .header-cont{
        text-align: center;
        .cont-icon{
          width: torem(200px);
          height: torem(180px);
          padding-top: torem(20px); 
          background-color: #eaeaea;
          border-radius: 50%;
          margin: 0 auto;
          overflow: hidden;
          .icon-head-1{
            font-size: torem(200px);
            color: #fff;

          }
        }
        .btn-login{
          margin-top: torem(30px);
