            color: #fff;
            margin-top: torem(20px);
          }
        }
      }
    }
    /*中间功能栏end*/
    /*底部功能栏start*/
    .my-floor{
      padding: 0 torem(42px);
      .fl-item{
        display: flex;
        justify-content: space-between;
        height: torem(150px);
        line-height: torem(150px);
        font-size: torem(40px);
        font-weight: bold;
        border-bottom: 2px solid #e3e3e3;
        .icon-right{
