<template>
  <div class="news-1">
	   <!-- 新闻内容start -->
	<div class="news">
	  <div class="news-item" v-for="art in articals">
	     <div class="news-text">
	      <h3>{{art.title}}</h3>
	      <div class="text-content">
	        <div class="left">
	          <span class="active-color">{{art.onTop}}</span>
	          <span>{{art.newTitle}}</span>
	          <span>{{art.num}}评</span>
	        </div>
	        <div class="right">
	          <span>...</span>
	        </div>
	      </div>
	    </div>
	    <div class="news-img">
	      <img :src="art.avatar" alt="">
	    </div>
	  </div>
	</div>
	<!-- 新闻内容end -->
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'News',
  data: function(){
  	return {

      articals:[]
  	// 	articals: [
			// {id:1,title:'习近平应约同特朗普通电话',avatar:'../../../static/images/news-1/0.jpg',onTop:'置顶',newTitle:'央视新闻',num:'3217'},
			// {id:1,title:'普京到底骑没骑过熊？ 本人终于亲自回应了',avatar:'../../../static/images/news-1/1.jpg',onTop:'',newTitle:'央视新闻',num:'37'},
			// {id:1,title:'习近平：我们的工作必须夯实基层',avatar:'../../../static/images/news-1/2.jpg',onTop:'',newTitle:'央视新闻',num:'417'},
			// {id:1,title:'科技部部长万钢记者会上大赞外籍记者：你中文说的真好！',avatar:'../../../static/images/news-1/3.jpg',onTop:'置顶',newTitle:'央视新闻',num:'13177'},
			// {id:1,title:'马斯克就中美电动汽车问题请示特朗普 科技部回应',avatar:'../../../static/images/news-1/4.jpg',onTop:'',newTitle:'央视新闻',num:'566'},
			// {id:1,title:'科技部部长万钢：一些评价制度不合适 医生还要写论文',avatar:'../../../static/images/news-1/5.jpg',onTop:'',newTitle:'央视新闻',num:'1237'},
			// {id:1,title:'白宫启动筹备特朗普与金正恩会晤，能否实现见面仍是未知数',avatar:'../../../static/images/news-1/6.jpg',onTop:'',newTitle:'央视新闻',num:'304'},
			// {id:1,title:'蒋介石灵柩修复情况首曝光 重新开放无明确时间表',avatar:'../../../static/images/news-1/7.jpg',onTop:'',newTitle:'央视新闻',num:'3867'},
			// {id:1,title:'蒋介石灵柩修复情况首曝光 重新开放无明确时间表',avatar:'../../../static/images/news-1/8.jpg',onTop:'置顶',newTitle:'央视新闻',num:'32204'}
  	// 	]
  	}
  },
  mounted(){
    axios.get('../../../static/data/news.json').then((res)=>{
      this.articals = res.data;
    }).catch(function(err){
      console.log(err);
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../../assets/css/torem.scss';
    /*新闻内容start*/

    .news{
        .news-item{
          position: relative;
          display: flex;
          justify-content: space-between;
          height: torem(200px);
          padding: torem(34px) torem(46px);
          &::after{
            position: absolute;
            bottom: 0;
            content:"";
            display: block;
            width: 92%;
            height: 3px;
            background-color: #e5e5e5;
          }
          .news-text{
            display: flex;
            flex-wrap: wrap;
            align-content: space-between;
            width: torem(660px);
            height: torem(200px);
            background-color: #fff;
            h3{
              width: 100%;
              font-size: torem(40px);
              line-height: torem(50px);
              color: #333;
              font-weight: bold;
            }
            .text-content{
              flex: 1;
              display: flex;
              justify-content: space-between;
              .left{
                span{
                  color: #8d8d8d;
                  font-size: torem(28px);
                  margin-right: torem(20px);
                  font-weight: bold;
                }
                .active-color{
                  color: #1d7eca;
                }
              }
              .right{
                span{
                  color: #d8d8d8;
                  font-weight: bold;
                  font-size: torem(40px);
                }
              }
            }
          }
          .news-img{
            width: torem(296px);
            height: torem(200px);
            img{
              width: 100%;
              height: 100%;
            }
          }
        }
     }
     /*新闻内容end*/

</style>


