<template>
  <div class="show">
    <!-- 详情页头部栏start -->
    <div class="header">
      <div class="head-top">
        <router-link to="/love" tag="i" class="iconfont icon-left"></router-link>
        <span class="head-rg">...</span>
      </div>
    </div>
    <!-- 详情页头部栏end -->
    <!-- 详情内容栏start -->
    <div class="show-content">
      <h3 class="cont-title">德云社那么多人，为什么岳云鹏最火？</h3>
      <div class="cont-middle">
        <img class="cont-img" src="../../static/images/live/1.png" alt="">
        <div class="cont-tit">解析12星座</div>
        <div class="cont-time">
          <span class="time-year">03-13</span>
          <span class="time-hour">11:39</span>
        </div>
      </div>
      <div class="cont-text">
        <p class="text">虽然娱乐圈很现实，但这里也从不缺有血性的人，而郭德纲和他的大部分徒弟们无疑就是其中较为鲜明的代表。</p>   
        <p class="text"><img class="text-img" src="../../static/images/live/10.jpg" alt=""></p>
        <p class="text">郭德纲是苦出身，也没啥背景，拼着一口气做出了德云社，却没想到因为自己“不慎言”的毛病，差点让这番心血付之东流，虽然如今我们知道最后是有惊无险，但人没有预知的能力，而彼时的郭德纲自然是惶恐不已。</p>
        <p class="text"><img class="text-img" src="../../static/images/live/11.jpg" alt=""></p>
        <p class="text">都说日久见人心，殊不知“麻烦”的测试能力也是一点都不输，彼时的老郭头大如牛，却没有想到爱徒曹云金竟也在这个时候选择“出走”，而理由是不愿意签合同，这自然是让他倍感打击。</p>
      </div>
    </div>
    <!-- 详情内容栏end -->
    <!-- 底部评论栏start -->
    <div class="floor">
      <div class="floor-lf">
        <i class="iconfont icon-write"></i>
        <input class="review" type="text" value="" placeholder="我来说几句">
      </div>
      <div class="floor-rg">
        <i class="iconfont icon-session"></i>
        <i class="iconfont icon-share"></i>
      </div>
    </div>

    <!-- 底部评论栏end -->
  </div>
</template>

<script>
export default {
  name: 'Show',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../assets/css/torem.scss';
  .show{
    .header{
      width: 100%;
      height: torem(148px);
      border-bottom: 2px solid #c7c7c7;
      .head-top{
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        justify-content: space-between;
        width: 100%;
        height: torem(148px);
        line-height: torem(148px);
        z-index: 10;
        background-color: #fff;
        .icon-left{
          font-size: torem(50px);
          font-weight: bold;
          color: #333;
          margin-left:torem(48px);
        }
        .head-rg{
          font-size: torem(50px);
          font-weight: bold;
          color: #333;
          letter-spacing: torem(10px);
          margin-right:torem(48px);
        }
      }
    }
    .show-content{
      padding: torem(30px) torem(52px);
      background-color: #f8f8f8;
      .cont-title{
        font-size: torem(60px);
        font-weight: bold;
        letter-spacing: torem(5px);
        line-height: torem(70px);
      }
      .cont-middle{
        display: flex;
        position: relative;
        line-height: torem(140px);
        color: #c7c7c7;
        font-size: torem(40px);
        border-bottom: 2px solid #dfdfdf;
        .cont-img{
          width: 70px;
          height: 70px;
          margin-top: torem(36px);
          margin-right: torem(20px);
        }
        .cont-tit{
          margin-right: torem(36px);
        }
      }
      .cont-text{
        .text{
          font-size: torem(46px);
          color: #131313;
          line-height: torem(80px);
          margin-bottom: torem(50px);
          .text-img{
            width: 100%;
          }
        }
      }
    }
    .floor{
      position: fixed;
      bottom: 0;
      left: 0;
      z-index: 10;
      display: flex;
      justify-content: space-between;
      padding: torem(20px) torem(52px);
      height: torem(96px);
      border-top: 3px solid #e1e1e1;
      background-color: #fff;
      .floor-lf{
        .icon-write{
          display: inline-block;
          width: torem(96px);
          height: torem(96px);
          vertical-align: top;
          text-align: center;
          line-height: torem(96px);
          font-size: torem(50px);
          color: #737373;
        }
        .review{
          height: torem(96px);
          width: torem(500px);
          font-size: torem(50px);
          color: #b7b7b7;
          font-weight: bold;
          border: none;
          &::placeholder{
            color: #b7b7b7;
            font-weight: bold;
          }
        }
      }
      .floor-rg{
        display: flex;
        width: torem(310px);
        height: torem(96px);
        line-height: torem(96px);
        justify-content: space-between;
        border-left: 3px solid #e4e4e4;
        .iconfont{
          font-size: torem(60px);
          margin: 0 torem(30px);
          color: #737373;
        }
      }
    }
  }
</style>



