<template>
  <div class="search">
    <header class="search-header">
      <div class="search-cont">
        <i class="iconfont icon-search"></i>
        <input type="text" value="" placeholder="请输入关键词">
      </div>
      <router-link to="/love" tag="div" class="search-remove">取消</router-link>
    </header>
    <div class="search-content">
      <div class="content-header">可以搜索到以下内容</div>
      <div class="search-icon">
        <div class="search-item">
          <i class="iconfont icon-circle"></i>
          <p>腾讯新闻</p>
        </div>        
        <div class="search-item">
          <i class="iconfont icon-text1"></i>
          <p>全网新闻</p>
        </div>        
        <div class="search-item">
          <i class="iconfont icon-lang"></i>
          <p>腾讯微博</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Search',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../assets/css/torem.scss';
  .search{
    .search-header{
      display: flex;
      justify-content: space-between;
      padding: torem(24px) 0;
      width: torem(990px);
      height: torem(96px);
      line-height: torem(96px);
      margin: 0 auto;
      border-bottom: 2px solid #e2e2e2; 
      .search-cont{
        display: flex;
        height: torem(96px);
        background-color: #f9f9f9;
        width: torem(850px);
        border-radius: torem(5px);
        .icon-search{
          height: torem(96px);
          width: torem(96px);
          font-size: torem(50px);
          text-align: center;
          line-height: torem(96px);
        }
        input{
          border: none;
          height: torem(96px);
          width: torem(500px);
          background: #f9f9f9;
          font-size: torem(50px);
          font-weight: bold;
          color: #ababab;
          &::placeholder{
           color: #ababab; 
          }
        }
      }
      .search-remove{
        font-size: torem(50px);
        font-weight: bold;
        color: #2b61bf;
      }
    }
    .search-content{
      padding: torem(88px) torem(226px);
      .content-header{
        height: torem(170px);
        text-align: center;
        line-height:torem(170px);
        font-weight: bold;
        font-size: torem(50px);
        color: #74787b;
        letter-spacing: torem(10px);
        border-bottom: 2px solid #e2e2e2; 
      }
      .search-icon{
        display: flex;
        justify-content: space-around;
        padding-top: torem(50px);
        .search-item{
          text-align: center;
          .iconfont{
            font-size: torem(150px);
            color: #ababab;
          }
          p{
            margin-top: torem(54px);
            color: #ababab;
            font-size: torem(40px); 
          }
        }
      }
    }
  }

</style>



