<template>
  <div class="check">
    <!-- 分类头部栏start -->
    <header class="recommend">
      <h3 class="title">编辑频道</h3>
      <router-link to="/" tag="div" class="fixed-rg" ><i class="iconfont icon-close"></i></router-link>
    </header>
    <!-- 分类页头部栏end -->
    <!-- 分类筛选栏start -->
    <div class="type">
      <div class="type-head">
        <div class="type-lf">已选频道</div>
        <div class="type-lf">按住拖动 调整排序</div>
      </div>
      <ul class="type-cont">
        <li class="ty-item" v-for="(type,tyKey) in types" @click="typeBtn(tyKey)" :class="{'ty-active':tyIndex==tyKey}">{{type}}</li>
      </ul>
      <!-- 分类添加栏start -->
      <div class="type-add">
        <ul class="add-nav">
          <li class="nav-item" v-for="(nav,key) in navItems" @mouseenter="navEnter(key)"><span :class="{'nav-active': index==key}" >{{nav}}</span></li>
          <!-- <li class="nav-item"><span>地方新闻</span></li> -->
        </ul>
        <div class="add-content">
          <ul class="content-item" v-for="(like,likeKey) in likeArr" v-show="isShow=index == likeKey">
            <li class="arts-items" v-for="liked in like">
              <div class="arts-lf">{{liked}}</div>
              <div class="arts-rg">+</div>
            </li>
          </ul>
        </div>
      </div>
      <!-- 分类添加栏end -->
    </div>
    <!-- 分类筛选栏end -->


  </div>
</template>

<script>
export default {
  name: 'Check',
  data () {
    return {
      types: ['要闻','两会','北京','视频','娱乐','体育','NBA','财经','汽车','科技','军事','国际','时尚','独家','文化','游戏','图片','数码','星座','电影','教育','美容','动漫','理财'],
      index: 0,
      tyIndex: 0,
      isShow:false,
      navItems:['推荐频道','地方新闻'],
      likeArr:[
        ['新时代','房产','佛学','股票','新时代','新时代'],
        ['股票1','股票2','股票3','股票4','股票5','股票6']
      ]

    }
  },
  methods: {
    typeBtn: function(index){
      this.tyIndex = index;
    },
    navEnter: function(index){
      this.index = index;
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../../assets/css/torem.scss';
  .check{
     /*分类页头部栏start*/
    header.recommend{
      position: relative;
      width: 100%;
      height: torem(146px);
      background-color: #fff;
      border-bottom: 2px solid #cbcbcb;
      .title{
        line-height: torem(146px);
        font-size: torem(50px);
        color: #333;
        font-weight: bold;
        text-align: center;
      }
      .fixed-rg{
        position: absolute;
        top: 0;
        right: 0;
        line-height: torem(146px);
        margin-right: torem(40px);
        .icon-close{
          font-size: torem(60px);
          color: #676767;
          font-weight: bold;
        }
      }
    }
    /*分类页头部栏end*/
    /*分类筛选栏start*/
    .type{
      padding: 0 torem(50px);
      .type-head{
        display: flex;
        justify-content: space-between;
        height: torem(120px);
        line-height: torem(120px);
        font-size: torem(40px);
        color: #ababab;
        margin-bottom: torem(60px);
      }
      .type-cont{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: torem(20px) 0;
        .ty-item{
          width: torem(196px);
          height: torem(80px);
          border: 2px solid #e2e2e2;
          border-radius: torem(10px);
          margin-bottom: torem(42px);
          text-align: center;
          line-height: torem(80px);
          color: #060606;
          font-weight: bold;
        }
        .ty-active{
          color: #8c8c8c;
        }
      }

      .type-add{
        .add-nav{
          display: flex;
          height: torem(110px);
          border-bottom: 2px solid #dadada;
          .nav-item{
            width: 50%;
            text-align: center;
            line-height: torem(110px);
            font-size: torem(40px);
            span{
              padding: torem(26px);
              background-color: #fff;
            }
            .nav-active{
              border-bottom: 3px solid #1385fa;
            }
          }
        }
        .add-content{
          .content-item{
            .arts-items{
              display: flex;
              justify-content: space-between;
              height: torem(154px);
              line-height: torem(154px);
              border-bottom: 2px solid #ddd;
              font-size: torem(44px);
              font-weight: bold;
              .arts-rg{
                width: torem(154px);
                height: torem(154px);
                font-size: torem(80px);
                color: #027dff;
                text-align: center;
                line-height: torem(154px);
              }
            }
          }
        }
      }
    }
    /*分类筛选栏end*/
  }

</style>



