import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Love from '@/components/Love'
import Live from '@/components/Live'
import My from '@/components/My'

// 首页路由
import News from '@/components/Index/News'
import Recommend from '@/components/Index/Recommend'
import Meeting from '@/components/Index/Meeting'
import Sole from '@/components/Index/Sole'
import Vcd from '@/components/Index/Vcd'
import Beijing from '@/components/Index/Beijing'
import Amusement from '@/components/Index/Amusement'
import Basketball from '@/components/Index/Basketball'
import Games from '@/components/Index/Games'
import System from '@/components/Index/System'
import Finance from '@/components/Index/Finance'

// 详情页
import Show from '@/components/Show'
// 登录页路由
import Login from '@/components/My/Login'
import Set from '@/components/My/Set'

// 搜索页
import Search from '@/components/Search'
// 选择分类页
import Check from '@/components/Index/Check'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children: [
        {
          path: '/',
          name: 'News',
          component: News
        },
        {
          path: '/index/recommend',
          name: 'Recommend',
          component: Recommend
        },        
        {
          path: '/index/meeting',
          name: 'Meeting',
          component: Meeting
        },        
        {
          path: '/index/sole',
          name: 'Sole',
          component: Sole
        },        
        {
          path: '/index/vcd',
          name: 'Vcd',
          component: Vcd
        },        
        {
          path: '/index/beijing',
          name: 'Beijing',
          component: Beijing
        },        
        {
          path: '/index/amusement',
          name: 'Amusement',
          component: Amusement
        },        
        {
          path: '/index/basketball',
          name: 'Basketball',
          component: Basketball
        },        {
          path: '/index/games',
          name: 'Games',
          component: Games
        },        {
          path: '/index/system',
          name: 'System',
          component: System
        },        {
          path: '/index/finance',
          name: 'Finance',
          component: Finance
        }
      ]
    },
    {
      path: '/check',
      name: 'Check',
      component: Check,
    },   
    {
      path: '/love',
      name: 'Love',
      component: Love
    },   
    {
      path:'/show',
      name:'Show',
      component: Show
    },   
    {
      path: '/live',
      name: 'Live',
      component: Live
    },    
    {
      path: '/my',
      name: 'My',
      component: My
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/set',
      name: 'Set',
      component: Set
    },
    {
      path:'/search',
      name:'Search',
      component: Search
    }
  ]
})



