<template>
  <div class="container">
    <!--  头部导航栏start -->
     <nav class="header">
        <div class="swiper-container nav">
          <div class="swiper-wrapper">
             <router-link tag="div" to="/" class="swiper-slide" :class="(indexRes =='' || indexRes == null) ? 'active' : ''"><span>要闻</span></router-link>
             <router-link tag="div" to="/index/recommend" class="swiper-slide" :class="indexRes =='recommend' ? 'active' : ''"><span>推荐</span></router-link>
             <router-link tag="div" to="/index/meeting" class="swiper-slide" :class="indexRes =='meeting' ? 'active' : ''"><span>两会</span></router-link>
             <router-link tag="div" to="/index/sole" class="swiper-slide" :class="indexRes =='sole' ? 'active' : ''"><span>独家</span></router-link>
             <router-link tag="div" to="/index/vcd" class="swiper-slide" :class="indexRes =='vcd' ? 'active' : ''"><span>视频</span></router-link>
             <router-link tag="div" to="/index/beijing" class="swiper-slide" :class="indexRes =='beijing' ? 'active' : ''"><span>北京</span></router-link>
             <router-link tag="div" to="/index/amusement" class="swiper-slide" :class="indexRes =='amusement' ? 'active' : ''"><span>娱乐</span></router-link>
             <router-link tag="div" to="/index/basketball" class="swiper-slide" :class="indexRes =='basketball' ? 'active' : ''"><span>NBA</span></router-link>
             <router-link tag="div" to="/index/system" class="swiper-slide" :class="indexRes =='system' ? 'active' : ''"><span>星座</span></router-link>
             <router-link tag="div" to="/index/finance" class="swiper-slide" :class="indexRes =='finance' ? 'active' : ''"><span>财经</span></router-link>
             <router-link tag="div" to="/index/games" class="swiper-slide" :class="indexRes =='games' ? 'active' : ''"><span>冬奥会</span></router-link>
          </div>
        </div>
        <router-link tag="div" to="/check" class="lf-btn">
          <span>+</span>
        </router-link>
     </nav>
     <!-- 头部导航栏end -->
     
     <!-- 搜索栏start -->
    <div class="search">
      <div class="search-content">
        <i class="iconfont icon-search"></i>
        <input class="sear" type="text" value="" placeholder="新闻搜索">
      </div>
    </div>
    <!-- 搜索栏end -->
      <router-view/>


      <!-- 底部固定栏引入 -->
      <footerNav></footerNav>
    </div>
</template>

<script>
import footerNav from '@/components/public/FooterNav';

export default {
  name: 'Index',
  data () {
    return {
      msg: '你好',
      name: 'asdfadsfads'
    }
  },
  computed:{
    indexRes: function(){
     var arr1 = this.$route.path.split('/');
      return arr1[2];
      
    }
  },
  mounted(){
      var swriper = new Swiper('.nav', {
        // 每个视图展示5个
          slidesPerView: 7.5,
          slidesPerGroup: 1
      })
      $(window).scroll(function(){
        var scrollTop = $(window).scrollTop();
        if( scrollTop > 0){
          $('.header').addClass('fixedTop');
        }else{
          $('.header').removeClass('fixedTop');
        }

      })
  },
  components:{
    footerNav
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../assets/css/torem.scss';
  .container{
    position: relative;
    width: 100%;

    /*头部导航栏start*/

    .header{
      overflow: hidden;
      height: torem(148px);
      padding: 0 torem(40px);
      background-color: #fff;
      border-bottom: torem(2px) solid #bebebe;
      .nav{
        font-size: torem(40px);
        font-weight: bold;
        color: #818a8a;
        height:  torem(148px);
        line-height: torem(148px);
        text-align: center;
        .active{
          span{
            color: #333;
            padding: torem(24px);
            border-bottom: 3px solid #2085f7;
          }
        }
      }
      .lf-btn{
        position: fixed;
        right: 0;
        top: 0;
        width: torem(120px);
        font-size: torem(100px);
        color: #2085f7;
        text-align: center;
        line-height: torem(148px); 
        background-color: #fff;
        box-shadow:torem(-10px) 0px torem(5px) 0px rgba(255,255,255,.8);
        z-index: 9
      }
    }
    .fixedTop{
      position: fixed;
      top: 0;
      left: 0;
      z-index: 5;
    }
     /*头部导航栏end*/
     /*搜索栏start*/
    .search{
      height: torem(92px);
      padding: torem(20px) torem(46px);
      .search-content{
        width: 100%;
        line-height: torem(92px);
        background-color: #fafafa;
        border-radius: torem(10px);
        .icon-search{
          display: inline-block;
          width: torem(84px);
          font-size: torem(50px);
          color: #8d8d8d;
          text-align: center;
          vertical-align: middle;
        }
        .sear{
          font-size: torem(50px);
          vertical-align: middle;
          border: none;
           background-color: #fafafa;
           &::placeholder{
            color: #c9c9c9;
           }
        }

      }
    }
     /*搜索栏end*/
 


  }
</style>


